
import { defineComponent, computed, ref, watch } from 'vue'
import { useStore } from '@/store'
// import { usePermission } from '@/hooks/use-permission'
import WjTable from '@/base-ui/table'
export default defineComponent({
  components: {
    WjTable
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    contentTableConfig: {
      type: Object,
      required: true
    },
    pageName: {
      type: String,
      required: true
    }
  },
  emits: ['newBtnClick', 'editBtnClick'],
  setup(props, { emit }) {
    const titles = ref(props.title)
    const store = useStore()

    //获取操作的权限
    // const isCreate = usePermission(props.pageName, 'create')
    // const isUpdate = usePermission(props.pageName, 'update')
    // const isDelete = usePermission(props.pageName, 'delete')
    // const isQuery = usePermission(props.pageName, 'query')

    //1.双向绑定pageInfo
    const pageInfo = ref({ currentPage: 1, pageSize: 10 })
    watch(pageInfo, () => {
      getPageData()
    })
    //2.发送网络请求
    const getPageData = (queryInfo: any = {}) => {
      // if (isQuery) return
      const { formOriginData, ...obj } = queryInfo
      store.dispatch('system/getPageListAction', {
        pageName: props.pageName,
        queryInfo: {
          offset: pageInfo.value.currentPage * pageInfo.value.pageSize - 10,
          size: pageInfo.value.pageSize,
          ...obj
        }
      })
    }
    getPageData()
    //3.获取用户列表
    const dataList = computed(() => {
      return store.getters[`system/pageListData`](props.pageName)
    })
    const dataCount = computed(() => {
      return store.getters[`system/pageListCount`](props.pageName)
    })
    //4.获取其他的动态插槽名
    const otherPropSlots = props.contentTableConfig?.propList.filter(
      (item: any) => {
        if (item.slotName === 'status') return false
        if (item.slotName === 'createAt') return false
        if (item.slotName === 'updateAt') return false
        if (item.slotName === 'handler') return false
        return true
      }
    )
    // 5.监听父组件的点击事件
    const handleNewClick = () => {
      emit('newBtnClick')
    }
    const editClick = (item: any) => {
      emit('editBtnClick', item)
    }
    return {
      dataList,
      getPageData,
      dataCount,
      pageInfo,
      titles,
      otherPropSlots,
      handleNewClick,
      editClick
      // isCreate,
      // isUpdate,
      // isDelete
    }
  }
})
